router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.isAuthenticated)) {
        if (!localStorage.getItem('access_token')) {
            next({ name: 'Login' });
            console.log('1');
        }else{
            next();
            console.log('2');
        }
    }else{
        if (localStorage.getItem('access_token')) {
            if(to.name == 'Login' || to.name == 'home'){
                next({ name: 'Dashboard' });
                console.log('3');
            }else{
                next();
                console.log('4');
            }
        }
    }
    console.log("5");
    next();

});